* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: transparent;
  width: 248px;
  height: 65px;
  line-height: 65px;
  display: inline-block;
  text-align: center;
  color: #000000;
  font-weight: 600;
  border: 2px solid #000000;
  text-decoration: none;
  cursor: pointer;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 70px;
}

.navigation__ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.navigation__ul.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.navigation__li {
  margin-bottom: 40px;
}

.navigation__a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
}

.navigation__b {
  margin-top: 80px;
}

.navigation__b_a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
}

.navigation__b_a:before {
  content: '';
  background-image: url('../img//svg/ico-phone.svg');
  width: 15px;
  height: 15px;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
}

.navigation__b_p {
  color: #FFFFFF;
  text-decoration: underline;
  margin-top: 20px;
  text-align: center;
}

.mobile-menu {
  display: block;
  width: 30px;
  height: 16px;
  margin-top: 14px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  position: relative;
  outline: none;
}

.mobile-menu.closer {
  position: fixed;
  z-index: 5;
  outline: none;
  top: 20px;
  right: 20px;
  border: none;
  transition: 1s;
}

.mobile-menu.closer:before,
.mobile-menu.closer:after {
  content: '';
  display: block;
  min-width: 100%;
  position: absolute;
  border: 1px solid #ffff;
  outline: none;
  transform-origin: 62% 62%;
  transition: 0.5s;
}

.mobile-menu.closer:before {
  transform: rotate(-45deg);
}

.mobile-menu.closer:after {
  transform: rotate(45deg);
}

.mobile-menu:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  transform: translate(0, -100%);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-bottom: 2px solid #000000;
}

.mobile-menu:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 20px;
  transform: translate(0, -50%);
  border-bottom: 2px solid #000000;
}

.catalog {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.catalog-paper__h {
  font-weight: 500;
  font-size: 28px;
  margin-bottom: 12px;
}

.catalog-paper__p {
  font-size: 14px;
  margin-bottom: 33px;
}

.catalog-paper__btn_a {
  margin-bottom: 85px;
}

.catalog-img {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: calc(66.66666666666667% - 20px);
}

.catalog-img__1 {
  position: absolute;
  top: -10px;
  left: 140px;
  width: calc(83.33333333333334% - 20px);
}

.catalog-img__2 {
  position: absolute;
  top: 90px;
  left: 80px;
  z-index: -1;
}

.catalog-img__3 {
  position: absolute;
  z-index: -2;
  top: 130px;
}

.catalog-img__4 {
  position: absolute;
  z-index: -4;
  top: 220px;
  left: 20px;
}

.catalog-svg {
  margin-bottom: 155px;
  width: calc(33.333333333333336% - 20px);
  align-self: center;
  list-style: none;
}

.catalog-svg__li {
  font-size: 10px;
  position: relative;
  margin: 60px 0;
}

.catalog-svg__li:before {
  content: '';
  position: absolute;
  width: 31px;
  height: 35px;
  top: -40px;
  left: 0;
}

.catalog-svg__li_choice:before {
  content: '';
  background-image: url(../img/svg/ico-choice.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.catalog-svg__li_price:before {
  content: '';
  background-image: url(../img/svg/ico-price.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.catalog-svg__li_exclusive:before {
  content: '';
  background-image: url(../img/svg/ico-exclusive.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.decorazza {
  margin-bottom: 105px;
  border-top: 1px solid transparent;
  width: 100%;
  height: 450px;
  background: url(../img/decor.jpg);
}

.decorazza-form {
  text-align: center;
  color: #FFFFFF;
  margin-top: 100px;
}

.decorazza-form__p:nth-child(1) {
  margin-bottom: 20px;
}

.decorazza-form__h {
  margin-bottom: 45px;
}

.decorazza-form__p:nth-child(3) {
  margin-bottom: 20px;
}

.advantages .background-image {
  background-image: url(../img/pictures5.jpg);
  background-repeat: no-repeat;
  background-size: 100% auto;
  margin-bottom: 75px;
  width: 100%;
  height: auto;
  position: relative;
}

.advantages .advantages-list,
.advantages .advantages-list_order {
  margin-bottom: 100px;
}

.advantages .advantages-list .decor-h,
.advantages .advantages-list_order .decor-h {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 35px;
}

.advantages .advantages-list .catalog-ul__li,
.advantages .advantages-list_order .catalog-ul__li {
  margin-bottom: 23px;
  list-style-type: none;
  position: relative;
}

.advantages .advantages-list .catalog-ul__li:before,
.advantages .advantages-list_order .catalog-ul__li:before {
  content: '';
  position: absolute;
  display: block;
  background-image: url(../img/Ellipse.svg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 6px;
  height: 6px;
  top: 7px;
  left: -15px;
}

.advantages .advantages-list .catalog-ul__li_p,
.advantages .advantages-list_order .catalog-ul__li_p {
  font-size: 14px;
}

.advantages .background-image__last {
  background-image: url(../img/pictures6.jpg);
  background-repeat: no-repeat;
  background-size: 100% auto;
  margin-bottom: 75px;
  width: 100%;
  height: 230px;
}

.plaster-advanteges__h {
  line-height: 40px;
  font-weight: 500;
  margin-bottom: 25px;
}

.plaster-advanteges__p {
  margin-bottom: 40px;
  font-size: 14px;
}

.plaster .wall {
  background-image: url(../img/wall.jpg);
  border-top: 0.1px solid transparent;
  width: 100%;
  height: 414px;
  margin-bottom: 300px;
}

.plaster .wall-plaster {
  margin-left: calc(16.666666666666668% + 10px);
  width: 50%;
  margin-bottom: 70px;
}

.plaster .wall-plaster__p {
  margin-top: 35px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
}

.plaster .wall-plaster__ul {
  padding-left: 32px;
}

.plaster .wall-plaster__li {
  list-style: none;
  margin-bottom: 12px;
  position: relative;
}

.plaster .wall-plaster__li:before {
  content: '';
  position: absolute;
  top: 0;
  left: -30px;
  background-image: url(../img/svg/ico-ok.svg);
  width: 16px;
  height: 13px;
}

.plaster .wall-plaster__li:nth-child(3):after {
  content: 'VS';
  position: absolute;
  border: 2px solid #000000;
  border-radius: 100px;
  font-weight: 600;
  font-size: 18px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #000000;
  background-color: white;
  top: 45px;
  left: -45px;
}

.plaster .wall-wallpaper {
  margin-left: calc(16.666666666666668% + 10px);
  width: 50%;
}

.plaster .wall-wallpaper__p {
  color: #FFFFFF;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 18px;
}

.plaster .wall-wallpaper__ul {
  padding-left: 32px;
}

.plaster .wall-wallpaper__li {
  color: #FFFFFF;
  list-style: none;
  margin-bottom: 15px;
  position: relative;
}

.plaster .wall-wallpaper__li:before {
  content: '';
  position: absolute;
  top: 0;
  left: -30px;
  background-image: url(../img/svg/Vector.svg);
  background-repeat: no-repeat;
  width: 16px;
  height: 13px;
}

.contacts-info__h {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 34px;
}

.contacts-info__p {
  margin-bottom: 25px;
}

.contacts-info__p:nth-child(5) {
  margin-bottom: 35px;
}

.contacts-info__link {
  text-decoration: none;
  color: #000000;
}

.contacts-info__phone:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-phone2.svg);
  width: 12px;
  height: 16px;
}

.contacts-info__mail:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-email.svg);
  width: 18px;
  height: 12px;
}

.contacts-info__adress:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-adress.svg);
  width: 13px;
  height: 18.5px;
}

.contacts-info__a {
  display: inline-block;
  margin-right: 9px;
  padding-bottom: 30px;
}

.contacts-info__a_vk:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-vk.svg);
  width: 20px;
  height: 20px;
}

.contacts-info__a_face:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-FB.svg);
  width: 20px;
  height: 20px;
}

.contacts-info__a_inst:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/ico-instagram.svg);
  width: 20px;
  height: 20px;
}

.contacts-map {
  width: 100%;
  height: auto;
  padding-bottom: 40px;
}

.contacts-map__img {
  width: 100%;
}

.footer-decorazza {
  color: #333333;
  font-size: 14px;
  text-align: center;
}

@media screen and (min-width: 10px) {
  .navigation__ul {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .catalog-paper {
    margin-top: 70px;
    margin-right: auto;
    position: static;
  }

  .catalog-paper__h {
    font-size: 38px;
    font-weight: 500;
    margin-bottom: 38px;
    font-size: 43px;
    width: 528px;
    position: relative;
  }

  .catalog-paper__p {
    margin-bottom: 38px;
    width: 433px;
    position: relative;
  }

  .catalog-img {
    height: 700px;
    margin-left: auto;
  }

  .catalog-img__2 {
    z-index: -1;
    top: 170px;
    left: 190px;
    width: calc(83.33333333333334% - 20px);
  }

  .catalog-img__3 {
    width: calc(83.33333333333334% - 20px);
    z-index: 2;
    top: 305px;
    left: -30px;
    overflow: visible;
  }

  .catalog-img__4 {
    width: calc(83.33333333333334% - 20px);
    z-index: -1;
    top: 470px;
    left: 50px;
    overflow: visible;
  }

  .catalog-svg {
    order: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: calc(83.33333333333334% - 20px);
  }

  .catalog-svg__li:before {
    content: '';
    position: absolute;
    width: 31px;
    height: 35px;
    top: -10px;
    left: -44px;
  }

  .decorazza-form__p:nth-child(1) {
    font-size: 16px;
  }

  .decorazza-form__h {
    font-size: 6vw;
  }

  .advantages {
    display: flex;
    flex-wrap: wrap;
  }

  .advantages .background-image {
    order: 0;
    background-size: 100% auto;
    width: 300px;
    height: auto;
    margin: 0;
  }

  .advantages .background-image:before {
    content: '';
    overflow: hidden;
    position: absolute;
    top: 180px;
    left: 55px;
    z-index: -1;
    background-image: url(../img/picture7.jpg);
    background-repeat: no-repeat;
    width: 300px;
    height: 300px;
  }

  .advantages .advantages-list .catalog-ul__li_p,
  .advantages .advantages-list_order .catalog-ul__li_p {
    line-height: 134.1%;
    margin: 0 auto;
  }

  .advantages-list {
    box-sizing: border-box;
    word-wrap: break-word;
    margin-left: 10px;
    margin-right: 10px;
    width: calc(58.333333333333336% - 20px);
    order: -1;
  }
}

@media screen and (min-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .catalog-paper {
    width: calc(41.66666666666667% - 20px);
  }

  .catalog-img {
    width: calc(58.333333333333336% - 20px);
  }
}